@charset "UTF-8";
/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Mono';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-v7-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/pt-mono-v7-latin_cyrillic-regular.woff") format("woff");
  /* Modern Browsers */ }

/* Общие стили, которые влияют и на десктопные разрешения */
html {
  box-sizing: border-box; }

body {
  min-width: 1400px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", Arial, sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.top-background {
  background: url("../img/top-background.png") #1B2931; }

.container {
  width: 1400px;
  margin-left: auto;
  margin-right: auto; }

.header {
  display: flex;
  min-height: 122px; }
  .header__logo {
    padding-top: 33px;
    padding-left: 1px; }
  .header__menu {
    padding-top: 52px;
    padding-left: 119px; }
  .header__menu:before {
    content: url("../img/top-menu.svg"); }

.logo__link:before {
  content: url("../img/top-logo.svg"); }

.email {
  padding-top: 50px;
  padding-left: 415px; }
  .email__link {
    text-decoration: none;
    color: #ffffff; }
  .email__link:before {
    content: url("../img/email-icon.svg");
    position: relative;
    top: 2px;
    right: 20px; }

.phone {
  padding-top: 46px;
  padding-left: 104px; }
  .phone__link {
    text-decoration: none;
    color: #ffffff; }
  .phone__link:before {
    content: url("../img/phone-icon.svg");
    position: relative;
    top: 4px;
    right: 16px; }

@media (max-width: 1023px) {
  /* Стили, влияющие на экраны ДО 1023px (Планшет) */
  .header {
    display: flex; }
    .header__logo {
      padding-top: 33px;
      padding-left: 1px; }
    .header__logo:before {
      content: url("../img/top-logo_tablet.svg"); }
    .header__menu {
      padding-top: 52px;
      padding-left: 119px; }
    .header__menu:before {
      content: url("../img/top-menu.svg"); } }

@media (max-width: 767px) {
  /* Стили, влияющие на экраны ДО 767px (Мобильные) */ }
